import React from 'react';
import {Button, Intent, Popover, PopoverInteractionKind, Position, ButtonGroup} from '@blueprintjs/core';
import {withRouter} from 'react-router-dom';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {deleteNetwork, editNetwork, loadNetworks} from '../../../../actions/NetworksActions';
import _isEqual from 'lodash/isEqual';
import {DELETE, EDIT} from "../../../../api/constants/Actions";
import _find from 'lodash/find';
import {NETWORK} from "../../../../api/constants/Entities";
import {isFormValid} from "../../../../helpers";
import {DELETE_NETWORK, EDIT_NETWORK} from "../../../../helpers/constants";
import Toaster from "../../../Common/Toaster";
import Can from "../../../../helpers/Can";
import { SETTINGS } from '../../../../api/constants/Pages';

class View extends React.Component{

    state = {
        network: {},
        networkBuffer: {},
        networkRaw: {},
        confirmDelete: false
    };

    static getDerivedStateFromProps(nextProps, prevState){
        const {networkRaw: oldNetworkRaw} = prevState;
        const {networks, match: {params: {id}}} = nextProps;
        let networkRaw = _find(networks, {id: Number.parseInt(id, 10)}) || {};
        const network = {...networkRaw};

        if(_isEqual(oldNetworkRaw, networkRaw))
            return null;

        return {network, networkBuffer: {...network}, networkRaw};
    }

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history, authUser: {token}, loadNetworks} = this.props;

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            loadNetworks(token, {});
            const network = this.state.network.operator.toUpperCase();

            if(OPERATION_SUCCESSFUL.action === EDIT_NETWORK){
                history.goBack();
                Toaster.show({
                    message: `${network} Successfully Updated ðŸ˜ƒ`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }

            if(OPERATION_SUCCESSFUL.action === DELETE_NETWORK){
                history.goBack();
                Toaster.show({
                    message: `${network} Successfully Deleted ðŸ˜ƒ`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            const network = this.state.network.operator.toUpperCase();
            if(OPERATION_FAILED.action === EDIT_NETWORK){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Update ${network} ðŸ˜ž`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
            if(OPERATION_FAILED.action === DELETE_NETWORK){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Delete ${network} ðŸ˜ž`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState(() => {
            return {
                network: {...this.state.network, [name]: value}
            }
        });
    }

    _handleDeleteNetwork = () => {
        this.props.deleteNetwork(this.props.authUser.token, this.state.network.id);
        this.setState({confirmDelete: false});
    }

    _handleEditNetwork = () => {
        const {authUser: {token}, editNetwork} = this.props;
        const {network: {operator, type}, networkRaw: {id}} = this.state;

        let data = new FormData();

        data.append('operator', operator);
        data.append('type', type);

        editNetwork(token, id, data);
    }

    _isFormValid = () => {
        const {network: {operator, type}} = this.state;

        let required = {operator, type};

        return isFormValid(required);
    }

    render(){
        const {history, editingNetwork, deletingNetwork, authUser} = this.props;
        const {network: {operator, type}, networkBuffer, network, confirmDelete, networkRaw} = this.state;
        const disableSaveButton = !this._isFormValid() || _isEqual(network, networkBuffer);
        const networkNotFound = !networkRaw.id;

        const editActionAllowed = authUser.root ? true : Can.User(authUser).perform(EDIT, NETWORK, SETTINGS);
        const deleteActionAllowed = authUser.root ? true : Can.User(authUser).perform(DELETE, NETWORK, SETTINGS);

        return [
            <div className="bp3-dialog-body">

                <label className="bp3-label">
                    Operator
                    <input
                        value={operator || ''}
                        name={'operator'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Network Type
                    <input
                        value={type || ''}
                        name={'type'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
            </div>,
            <div className="bp3-dialog-footer">
                <div className="bp3-dialog-footer-actions">
                    {networkNotFound && (
                        <Button
                            intent={Intent.PRIMARY}
                            onClick={() => history.goBack()}
                            icon="undo"
                            text="Go Back"
                        />
                    )}

                    <ButtonGroup>
                        <Popover isOpen={this.state.confirmDelete} interactionKind={PopoverInteractionKind.CLICK} position={Position.BOTTOM}>
                            <Button
                                disabled={deletingNetwork || editingNetwork || networkNotFound || !deleteActionAllowed}
                                loading={deletingNetwork}
                                onClick={() => this.setState({confirmDelete: true})}
                                intent={Intent.DANGER}
                                text={'delete'}
                                icon="trash" />
                            <div style={{ padding: ".5rem", display: "flex", flexDirection: "column" }}>
                                <header>Are you sure?</header>
                                <div style={{ display: "flex", justifyContent: "space-evenly", marginTop: ".5rem" }}>
                                    <Button onClick={this._handleDeleteNetwork} small text="yes" intent={Intent.SUCCESS}/>
                                    <Button onClick={() => this.setState({confirmDelete: false})} small text="no" intent={Intent.DANGER}/>
                                </div>
                            </div>
                        </Popover>
                    </ButtonGroup>

                    <Button
                        disabled={disableSaveButton || deletingNetwork || networkNotFound || !editActionAllowed}
                        loading={editingNetwork}
                        intent={Intent.PRIMARY}
                        onClick={this._handleEditNetwork}
                        icon="tick"
                        text="save"
                    />
                </div>
            </div>
        ];
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        match: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        editNetwork: PropTypes.func.isRequired,
        deleteNetwork: PropTypes.func.isRequired,
        loadNetworks: PropTypes.func.isRequired,
        editingNetwork: PropTypes.bool.isRequired,
        deletingNetwork: PropTypes.bool.isRequired,
        networks: PropTypes.array.isRequired,
    };
}

const mapStateToProps = (
    {authUser, OPERATION_FAILED, OPERATION_SUCCESSFUL, editingNetwork, deletingNetwork, networks}) => (
        {authUser, OPERATION_FAILED, OPERATION_SUCCESSFUL, editingNetwork, deletingNetwork, networks});
const mapDispatchToProps = dispatch => bindActionCreators({deleteNetwork, editNetwork, loadNetworks}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(View));


